import './App.css';
import React, { useCallback, useEffect, useState } from 'react';
import TextInput from 'react-autocomplete-input';
import 'react-autocomplete-input/dist/bundle.css';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
// import { newDrugList } from './data/drugs'
import { newDrugList2 as newDrugList } from './data/drugs'
import RangeConv from './components/RangeConv';
import DirectConv from './components/DirectConv';
import { Button } from '@mui/material';

function App() {
  const availableOptions = [...newDrugList.map(e => e.name).sort()]

  const [mgInput, setmgInput] = useState('')
  const [mLInput, setmLInput] = useState('')
  const [shouldRefresh, setShouldRefresh] = useState(false)


  const [drug, setDrug] = useState()
  const [drugConc, setDrugConc] = useState()

  // not used anymore
  const [hasChooseConc, setHasChooseConc] = useState(false)
  const [dropdownOptions, setDropdownOptions] = useState([])

  const setChosenDrug = useCallback(() => {
    const selectedDrug = newDrugList.filter(e => e.name === drug)
    // console.log(selectedDrug)

    if (selectedDrug.length > 0) {
      // will always go to the else condition since no dropdowns
      if (Array.isArray(selectedDrug[0].concIn_mg_per_mL)) {
        setDropdownOptions(
          selectedDrug[0].concIn_mg_per_mL.map(item => ({
            value: item,
            label: item + ' mg/mL'
          }))
        )
        setHasChooseConc(true)
      } else {
        setHasChooseConc(false)
        setDrugConc(selectedDrug[0].concIn_mg_per_mL)
      }
    }
  }, [drug])


  useEffect(() => {

    setChosenDrug()
    console.log('mLinput')

    const hasInput = (mLInput != null && mLInput !== '' && +mLInput !== 0)
    console.log('mLinput', hasInput, mLInput)

    if (shouldRefresh) {
      setShouldRefresh(false)
      if (hasInput &&
        drugConc !== '' &&
        drugConc != null) {
        const nums = drugConc.split('/').map(num => parseInt(num))
        const concInNum = nums[0] / nums[1]
        console.log('B')

        const weight = mLInput * concInNum
        setmgInput(weight.toFixed(2))
      } else {
        console.log('clearing..')
        setmgInput('')
      }
    }

  }, [mLInput, drug])



  useEffect(() => {
    setChosenDrug()
    console.log('mginput')

    const hasInput = (mgInput != null && mgInput !== '' && +mgInput !== 0)
    console.log('mginput', hasInput, mgInput)

    if (shouldRefresh) {
      setShouldRefresh(false)
      if (hasInput &&
        drugConc !== '' &&
        drugConc != null) {
        const nums = drugConc.split('/').map(num => parseFloat(num))
        const concInNum = nums[0] / nums[1]
        console.log('A')

        const vol = mgInput / concInNum
        setmLInput(vol.toFixed(2))

      } else {
        console.log('clearing..')
        setmLInput('')
      }
    }
  }, [mgInput, drug])


  // removes concentration display when drug name is no longer valid
  useEffect(() => {
    if (!newDrugList.map(d => d.name).includes(drug)) {
      setDrugConc('')
    }

  }, [drug])

  const handleChange = (type, { target }) => {
    if (target.value === '') {
      setmgInput('')
      setmLInput('')
    } else {
      setShouldRefresh(true)
      if (type === 'mL') {
        setmLInput(target.value)
      } else {
        setmgInput(target.value)
      }
    }

  }

  const handleDrugInput2 = (drugName) => {
    // console.log(drugName)
    drugName = drugName.replace('@', '')
    setDrug(drugName)
    // handleRequest()
  }

  // not used anymore
  const handleDropdownSelect = (chosenOption) => {
    // console.log(chosenOption)
    setDrugConc(chosenOption.value)
  }

  const formatDrugConcentration = () => {
    const drugNum = drugConc.split('/')
    return newDrugList.find(d => d.name === drug)?.units ? `${drugNum[0]}units/${drugNum[1]}mL` : `${drugNum[0]}mg/${drugNum[1]}mL`
    // return `${drugNum[0]}mg/${drugNum[1]}mL`
  }

  const clearInput = () => {
    mL_mg_clear()
    setDrug('')

    clearAdvanced()
  }

  const clearAdvanced = () => {
    setRangeInput('')
    setChosenObj({})
    setIsUnitWeight(false)
    setRangeResult('')
  }

  const mL_mg_clear = () => {
    setmLInput('')
    setmgInput('')
  }



  // logic for rangeConv

  const [rangeResult, setRangeResult] = useState('')
  const [rangeInput, setRangeInput] = useState('')
  const [isUnitWeight, setIsUnitWeight] = useState(false)
  const [chosenObj, setChosenObj] = useState({})

  useEffect(() => {
    console.log(drug)
    if (!drug) {
      console.log('clearing..')
      clearInput()
    }
  }, [drug])

  useEffect(() => {
    let upperVal
    let lowerVal
    let warnings = []

    // const chosenDrug = newDrugList.find(d => d.name === drug)

    // console.log(chosenDrug)
    console.log(rangeInput)
    console.log(chosenObj, isUnitWeight)

    if (chosenObj) {
      if (isUnitWeight) {
        const upperLim = chosenObj?.upper
        if (upperLim) {
          upperVal = upperLim * rangeInput
          if (upperVal >= chosenObj.max_dose) {
            warnings.push('upper')
            upperVal = chosenObj.max_dose
          }
        }
        lowerVal = chosenObj.lower * rangeInput
        if (lowerVal >= chosenObj.max_dose) {
          warnings.push('lower')
          lowerVal = chosenObj.max_dose
        }
      } else {
        upperVal = chosenObj?.upper
        lowerVal = chosenObj.lower
      }
    }

    // if (rangeInput != null &&
    //   rangeInput !== '' &&
    //   +rangeInput !== 0 &&
    console.log(chosenObj)
    const ismL = chosenObj?.ismL ? true : false
    console.log(ismL)
    if (!ismL &&
      drugConc !== '' &&
      drugConc != null &&
      Object.keys(chosenObj).length !== 0) {
      const upperWeight = upperVal
      const lowerWeight = lowerVal

      let drugConcz = drugConc
      // SPECIAL CASE FOR TMP
      if (chosenObj.type === 'TMP') {
        drugConcz = chosenObj.override
        console.log('using overridden conc', drugConcz)
      }
      const nums = drugConcz.split('/').map(num => parseFloat(num))
      const concInNum = nums[0] / nums[1]

      console.log(upperWeight, lowerWeight)

      const res = [
        { 'weight': lowerWeight, 'vol': lowerWeight / concInNum, 'warning': warnings.includes('lower'), 'label': 'lower-limit' },
        ...upperWeight ? [{ 'weight': upperWeight, 'vol': upperWeight / concInNum, 'warning': warnings.includes('upper'), 'label': 'upper-limit' }] : []
      ]

      console.log(rangeInput, drugConc, res)

      setRangeResult(res)
    }


    if (ismL &&
      drugConc !== '' &&
      drugConc != null &&
      Object.keys(chosenObj).length !== 0) {
      const upperVol = upperVal
      const lowerVol = lowerVal

      const nums = drugConc.split('/').map(num => parseFloat(num))
      const concInNum = nums[0] / nums[1]

      console.log(upperVol, lowerVol)

      const res = [
        { 'weight': lowerVol * concInNum, 'vol': lowerVol, 'warning': warnings.includes('lower'), 'label': 'lower-limit' },
        ...upperVol ? [{ 'weight': upperVol * concInNum, 'vol': upperVol, 'warning': warnings.includes('upper'), 'label': 'upper-limit' }] : []
      ]

      console.log(rangeInput, drugConc, res)

      setRangeResult(res)
    }

  }, [rangeInput, drugConc, chosenObj, isUnitWeight])


  const handleRangeInputChange = (inputVal) => {
    console.log(inputVal)
    setRangeInput(inputVal)
  }

  const handleRangeInputReset = () => {
    setRangeInput('')
  }

  const handleFinalSelection = ({ type, finalChoice }) => {
    type === 'unitWeight' && setIsUnitWeight(true)
    setChosenObj(finalChoice)
  }


  return (
    <div className='container'>
      <div className='subcontainer'>
        <label id='children' >
          <span id='title'>drug name:</span>
          <TextInput
            Component={'input'}
            trigger={['', '@']}
            options={availableOptions}
            spacer={''}
            // requestOnlyIfNoOptions={false}
            // onRequestOptions={handleDrugInput}
            onSelect={handleDrugInput2}
            onChange={handleDrugInput2}
            maxOptions={50}
            value={drug}
            className="inputBox"
          />
        </label>

        {drugConc && <p>concentration: {formatDrugConcentration()} </p>}

        {/* not used anymore */}
        {hasChooseConc &&
          <Dropdown options={dropdownOptions}
            onChange={handleDropdownSelect}
            // value={dropdownOptions.length > 0 ? dropdownOptions[0]: ''}
            placeholder="Select an option"
            className='dropdown'
          />
        }

      </div>

      <DirectConv
        onClearInputs={mL_mg_clear}
        onInputChange={handleChange}
        mLInput={mLInput}
        mgInput={mgInput}
      />

      <RangeConv
        selectedDrug={drug}
        result={rangeResult}
        // for inputbox when unitWeight is active..
        onChangeUnitWeightInput={handleRangeInputChange}
        rangeInput={rangeInput}
        onResetInput={handleRangeInputReset}
        onSetFinalSelection={handleFinalSelection}
      />
      {/* clear button */}
      <Button onClick={clearInput} >reset all</Button>
    </div>

  );
}

export default App;
