import React from 'react'
import { FormControl, InputLabel, MenuItem, Select } from '@mui/material';

export default function dropdown({ data, level, onDropDownSelect, value }) {
    console.log(data, level, value)
    let displayData = data

    if(level === 'second') {
        displayData = data.map(d => d.type)
    } else {
        displayData = Object.keys(data)
    }

    return (
        <FormControl fullWidth>
            <InputLabel>Options</InputLabel>
            <Select
                value={value}
                label="Options"
                onChange={(e) => onDropDownSelect(e, level)}
            >
                {displayData.map(o => <MenuItem value={o} key={o}>{o}</MenuItem> )}
            </Select>
        </FormControl>
    )
}