import React, { useState } from 'react'

import 'react-autocomplete-input/dist/bundle.css';
import 'react-dropdown/style.css';
// import { RemoveCircleOutlineIcon } from '@mui/icons-material';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import DropDown from '../ui/DropDown';

import './RangeConv.css'

import { newDrugList2 as newDrugList } from '../data/drugs'
import { FormControl, InputLabel, MenuItem, Select, TextField } from '@mui/material';
import { useEffect } from 'react';
import { useCallback } from 'react';

export default function RangeConv({ selectedDrug,
    result,
    onChangeUnitWeightInput,
    rangeInput,
    onResetInput,
    onSetFinalSelection }) {


    const chosenDrug = newDrugList.find(d => d.name === selectedDrug)
    // const isDisabled = !chosenDrug?.hasOwnProperty('dose_lower')
    const isDisabled = !chosenDrug?.hasOwnProperty('adv')

    const [variant, setVariant] = useState('')
    const [calculationOpts, setCalculationOpts] = useState('')

    // firstChoice = [[ weightRange | ageRange | unitWeight ]] array
    const [firstChoice, setFirstChoice] = useState('')
    // secondChoice = selected object within [[ weightRange | ageRange | unitWeight ]]
    const [secondChoice, setSecondChoice] = useState('')
    // firstHeader = [[ weightRange | ageRange | unitWeight ]] string
    const [firstHeader, setFirstHeader] = useState('')
    // firstHeader = type property as a string in [[ weightRange | ageRange | unitWeight ]] object
    const [secondHeader, setSecondHeader] = useState('')

    console.log(chosenDrug)
    console.log(calculationOpts)
    console.log(selectedDrug)
    console.log(result)

    useEffect(() => {
        const calculationOptswithVariant = { ...chosenDrug }?.adv

        if (calculationOptswithVariant) {
            console.log('setting variant')
            console.log(calculationOptswithVariant.variant)
            setVariant(calculationOptswithVariant.variant)

            const cleansedCalcOps = Object.keys(calculationOptswithVariant).
                filter((key) => !key.includes('variant')).
                reduce((cur, key) => { return Object.assign(cur, { [key]: calculationOptswithVariant[key] }) }, {});
            console.log(cleansedCalcOps)
            setCalculationOpts(cleansedCalcOps)
        } else {
            setFirstChoice('')
            setSecondChoice('')
            setFirstHeader('')
            setSecondHeader('')
            setVariant('')
            setCalculationOpts('')
        }
    }, [chosenDrug])


    const handleDropDownChange = (content, level) => {
        console.log(content)
        console.log(level)
        const chosenVal = content.target.value
        if (level === 'first') {
            console.log(chosenVal)
            console.log(level)
            console.log(calculationOpts[chosenVal])
            setFirstChoice(calculationOpts[chosenVal])
            setFirstHeader(chosenVal)
            setSecondHeader('')
            setSecondChoice('')
            onSetFinalSelection({ type: null, finalChoice: '' })
        } else {
            const finalChoice = firstChoice.find(c => c.type === chosenVal)
            setSecondChoice(finalChoice)
            setSecondHeader(chosenVal)
            console.log({ type: firstHeader, finalChoice: finalChoice })
            onSetFinalSelection({ type: firstHeader, finalChoice: finalChoice })
        }
        // setHasAdditionalOpts(calculationOpts[choice].length > 1)
    }

    const handleChange = (content) => {
        onChangeUnitWeightInput(content.target.value)

        // const chosenType = secondHeader ? secondHeader : 'default'
        // console.log(chosenType)

        const finalChoice = firstChoice.find(c => c.type === secondHeader)
        console.log(finalChoice)
        onSetFinalSelection({ type: firstHeader, finalChoice: finalChoice })
    }

    const formatRange = useCallback(() => {
        // const chosenDrug = newDrugList.find(d => d.name === selectedDrug)
        let endVal = ''
        console.log(secondChoice.lower)
        if (secondChoice) {
            if (secondChoice.upper) {
                endVal = `- ${secondChoice.upper}`
            }
            return `${secondChoice.lower} ${endVal}`
        }
        // return `${drugNum[0]}mg/${drugNum[1]}mL`
    }, [secondChoice])


    console.log(secondHeader)

    const handleDefaultChoice = (level) => {
        // console.log(level)
        if (level === 'first') {
            console.log(calculationOpts, firstChoice, firstChoice.length)
            if (firstChoice === '') {
                console.log('setting default first state')
                const k = Object.keys(calculationOpts)[0]
                console.log(calculationOpts[k])
                setFirstChoice(calculationOpts[k])
                setFirstHeader(k)
            }
            // setHasAdditionalOpts(calculationOpts[choice].length > 1)
        } else {
            if (secondChoice === '') {
                console.log('setting default second state')
                const k = Object.keys(firstChoice)[0]
                console.log(firstChoice[k])
                setSecondChoice(firstChoice[k])
                setSecondHeader(firstChoice[k].type)
            }
        }
    }

    return (
        <div className='subcontainer'>
            <div className='title'>Advanced</div>
            {/* <div style={{ backgroundColor: isDisabled ? '#5b6977' : '' }}> */}
            {/* <DropDown data={['a', 'b']} level={'upper'}/> */}

            {calculationOpts &&
                <>
                    {/* if upper level has multiple options */}
                    {
                        Object.keys(calculationOpts).length > 1 ?
                            <DropDown
                                data={calculationOpts}
                                level='first'
                                onDropDownSelect={handleDropDownChange}
                                value={firstHeader}
                            /> :
                            (handleDefaultChoice('first'))
                    }

                    {/* if lower level has multiple options */}
                    {
                        firstChoice &&
                        <>
                            {
                                firstChoice.length > 1 ?
                                    <DropDown
                                        data={firstChoice}
                                        level='second'
                                        onDropDownSelect={handleDropDownChange}
                                        value={secondHeader}
                                    /> :
                                    (handleDefaultChoice('second'))
                            }
                        </>
                    }
                </>
            }

            {/* dose range */}
            {secondChoice &&
                <p className='test'>per {variant}: {formatRange()}{firstHeader === 'unitWeight' ? 'mg/kg' : 'mg'} </p>
            }


            {/* weight of person */}
            {firstHeader === 'unitWeight' && secondHeader &&
                <label id='children'>
                    {/* <span id='title'>hooman weight:</span>
                <input
                    type='text'
                    onChange={handleChange}
                    value={rangeInput} className="inputBox"
                    disabled={isDisabled}
                    style={{ backgroundColor: isDisabled && 'red' }}
                /> */}
                    <TextField
                        label="hooman weight(kg)"
                        variant="outlined"
                        // helperText={'weight in kg'}
                        disabled={!secondHeader}
                        onChange={handleChange}
                        value={rangeInput}
                        size='small'
                    />
                    <RemoveCircleOutlineIcon fontSize='medium'
                        onClick={() => {
                            secondHeader && onResetInput()
                            secondHeader && console.log('clicked')
                        }}
                        color={!secondHeader ? 'disabled' : 'secondary'}
                    />

                    {/* <button
                    onClick={() => onResetInput()}
                    disabled={isDisabled}
                    style={{ color: isDisabled && 'red' }}
                >x</button> */}
                </label>
            }
            {secondHeader === 'TMP' &&
            <div>using overriden concentration 
                {firstChoice[0].override.split('/')
                .reduce((prev, curr, idx) => 
                    idx === 0? curr: ` ${prev}mg/${curr}mL` 
                )}</div>
            }


            {result && (<div className="resultContainer2">
                {result.map(r => (
                    <div className='rangeResults'>
                        <span className='test'>{r.label}</span>
                        <ul id='result'
                            key={Math.random()}
                            style={{ backgroundColor: r.warning ? 'red' : '' }}>
                            {r.weight.toFixed(2)}mg = {r.vol.toFixed(2)}mL
                        </ul>
                    </div>
                ))}
            </div>)}
            {/* </div> */}
        </div>
    );
}


