import React from 'react'
import 'react-dropdown/style.css';
import './DirectConv.css'
import Button from '@mui/material/Button';
import { InputAdornment, TextField } from '@mui/material';

export default function DirectConv({ onClearInputs, onInputChange, mgInput, mLInput }) {

    // console.log(mgInput)
    return (
        <div className='subcontainer'>
            <div className='doseBar'>
                <span style={{margin: '0px 5px'}}>Dose:</span>
                <TextField
                    // label="dose in (mg)"
                    id="outlined-start-adornment"
                    // sx={{ m: 1, width: '25ch' }}
                    InputProps={{
                        endAdornment: <InputAdornment position="end">mg</InputAdornment>,
                    }}
                    onChange={(content) => onInputChange('mg', content)}
                    // value={mgInput == '' ? '' : mgInput}
                    value={mgInput}
                    variant='outlined'
                    color='warning'
                    className='inputBox'
                    size='small'
                />
                <TextField
                    // label="dose in (mL)"
                    id="outlined-start-adornment"
                    // sx={{ m: 1, width: '25ch' }}
                    InputProps={{
                        endAdornment: <InputAdornment position="end">mL</InputAdornment>,
                    }}
                    onChange={(content) => onInputChange('mL', content)}
                    // value={mLInput == '' ? '' : mLInput}
                    value={mLInput}
                    variant='outlined'
                    color='secondary'
                    className='inputBox'
                    size='small'
                />
                {/* <span className='sub'>
                    <input type='text'
                        onChange={(content) => onInputChange('mg', content)}
                        // value={mgInput == '' ? '' : mgInput}
                        value={mgInput}
                        className="inputBox" />
                    mg
                </span>
                <span className='sub'>
                    <input type='text'
                        onChange={(content) => onInputChange('mL', content)}
                        // value={mLInput == '' ? '' : mLInput}
                        value={mLInput}
                        className="inputBox" />
                    mL
                </span> */}
            </div>
            <Button onClick={onClearInputs} style={{ marginTop: '15px' }}
                variant='contained' color='secondary' size='small'>clear</Button>
        </div>
    )
}
